import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  // date-time-picker
  public startDateData: string;
  public dateOptions: any = {
    autoclose: true,
    todayBtn: 'linked',
    todayHighlight: true,
    assumeNearbyYear: true,
    language: 'cn',
    hideIcon: true,
  };

  public timeOptions: any = {
    showMeridian: false,
    minuteStep: 1,
    hideIcon: true,
    defaultTime: 'current',
  };

  public constructor() {

  }

  test() {
    console.log(this.startDateData);
  }

}



